<template>
  <ul>
    <TodoItem
      v-for="todo in todos"
      v-bind:todo="todo"
      :key="todo.key"
      v-on:remove-todo="removeTodo"
    />
  </ul>
</template>
<script>
import TodoItem from "./TodoItem";

export default {
  name: "App",
  props: ["todos"],
  components: {
    TodoItem,
  },
  methods: {
    removeTodo(id) {
      this.$emit("remove-todo", id);
    },
  },
};
</script>

<style>
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
</style>
