<template>
  <div id="app">
    <h1>Todo Application</h1>
    <hr />
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      todos: [
        { id: 1, title: "Buy bfdsfeerff", completed: false },
        { id: 2, title: "Buy bread", completed: false },
        { id: 3, title: "Buy mouse", completed: false },
      ],
    };
  },
  methods: {
    removeTodo(id) {
      this.todos = this.todos.filter((item) => item.id !== id);
    },
    addTodo(title) {
      this.todos.push({
        id: this.todos.length,
        title: title,
        completed: false,
      });
    },
  },
};
</script>

